export const getHTML = (data: string): string => `<!DOCTYPE html>
<html>
	<head>
		<!-- Name of the page -->
		<title>TypeScript CodeHS Library</title>

		<!-- Image of the page -->
		<link rel="shortcut icon" type="image/png" href="/favicon.png" />

		<meta charset="utf8" />
		<style type="text/css">
			/* changes text to red */
			.noscript {
				color: red;
				font-size: 30px;
			}

			/* css for the canvas */
			#game {
				border: 1px solid black;
			}

			/* css for the console */
			#console {
				overflow: scroll;
				white-space: pre-wrap;
				padding: 10px;
				background-color: lightgrey;
				border: 1px solid black;
				height: 180px;
				width: 380px;
			}
		</style>
	</head>

	<body>
		<!-- Check if JavaScript is allowed -->
		<noscript>
			<p class="noscript">This page requires JavaScript on a modern browser to run</p>
		</noscript>

		<!-- Necessary for the library -->
		<div id="output"></div>

		<!-- Your code -->
    <script type="module">
${data
	.split("\n")
	.map((s) => `\t\t\t${s}`)
	.join("\n")}
    </script>

		<!-- script to check if browser allows modules -->
		<script nomodule>
			let out = document.getElementById("output");
			let t = document.createElement("p");
			t.innerHTML = "This page requires Chrome >= 61, Edge >= 16, Firefox >= 60, Opera >= 48, or Safari >= 10.1";
			t.className = "noscript";
			out.append(t);
		</script>
	</body>
</html>`;
